.jumbotron {
    position: relative;
    width: 100%;
    text-align: center;
    background:none;
    color: white;
    z-index: 1;
    
}
.jumbotron .bg {
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    position: absolute;
    background-image: url(http://i.imgur.com/i6mXo8I.jpg);
    background-size: 125%;
    z-index: 0;
    -webkit-filter: blur(.2px) grayscale(20%);
            filter: blur(.2px) grayscale(20%);
    border-radius: 0px 0px 15px 15px;
}

.content {
    position: relative;
}


